@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Inria+Sans:wght@300;400&family=Open+Sans:ital,wght@0,300;1,700&display=swap");
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  font-size: inherit;
}

body {
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input,
select {
  vertical-align: middle;
}

ul {
  list-style: none;
}

/**
 * Сброс стилей у кнопки.
 * Придётся немного поработать, чтобы получить нейтральный вид.
*/
button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  /* отображаем курсор в виде руки при наведении; некоторые
  считают, что необходимо оставлять стрелочный вид для кнопок */
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

h1 {
  font-weight: 400;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  font-family: "Inria Sans", sans-serif;
}

html,
body {
  height: 100%;
  color: #000;
  font-family: "Inria Sans", sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}

.container {
  margin: 0 auto;
}

/* 
font-family: 'Inria Sans',
sans-serif;
font-family: 'Open Sans',
sans-serif;
 */
.title {
  font-family: "Open Sans", sans-serif;
  line-height: 89%;
  color: #000;
  font-size: 36px;
  font-style: italic;
  font-weight: 700;
  /* 169.1px */
  letter-spacing: -4.75px;
}

.text {
  color: #000;
  font-family: "Open Sans", sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.container {
  max-width: 1628px;
  padding: 0 50px;
}

.nav {
  margin-top: 30px;
}
.nav__list {
  display: flex;
  max-width: 625px;
  justify-content: space-between;
  z-index: 2;
}
.nav__item {
  position: relative;
}
.nav__item::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #f060ab;
  opacity: 0;
  transition: all 0.3s linear;
}
.nav__item:hover.nav__item::after {
  opacity: 1;
  transform: rotate(360deg);
  width: 100%;
}
.nav__item:hover .nav__link {
  color: #f060ab;
}
.nav__link {
  font-weight: 300;
  transition: all 0.3s linear;
}

.first-screen {
  max-width: 1628px;
  padding: 0 50px;
  width: 100%;
  flex: 1 1 auto;
  margin: 0 auto 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}
.first-screen__title {
  font-family: "Open Sans", sans-serif;
  line-height: 89%;
  color: #000;
  font-size: 3.75rem;
  font-style: italic;
  font-weight: 700;
  /* 169.1px */
  letter-spacing: -4.75px;
}
.first-screen__text {
  margin-top: 13px;
  max-width: 754px;
}
.first-screen__links {
  margin-top: 57px;
  display: flex;
  gap: 27px;
}
.first-screen__link {
  display: block;
  font-family: "Open Sans", sans-serif;
  line-height: 89%;
  font-size: 1.42rem;
  font-style: italic;
  font-weight: 700;
  text-align: center;
  /* 30.26px */
  letter-spacing: 1.02px;
  transition: all 0.3s linear;
}
.first-screen__link--pink {
  color: #EFE7EE;
  flex-basis: 234px;
  padding: 24px 0;
  border-radius: 39px;
  background: #F060AB;
}
.first-screen__link--pink:hover {
  color: #f060ab;
  background: #efe7ee;
  transform: translateY(-10px);
  box-shadow: -1px 20px 20px;
}
.first-screen__link--empty {
  сolor: black;
  flex-basis: 234px;
  padding: 22px 0;
  border-radius: 39px;
  border: 2px solid #000;
  background: #FFF;
}
.first-screen__link--empty:hover {
  color: #ffffff;
  background: #000000;
  transform: translateY(-10px);
  box-shadow: 7px 9px 18px #000;
}
.first-screen__img {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  z-index: -1;
  width: 50%;
  max-width: 873px;
}

.box {
  display: flex;
  justify-content: space-between;
}
.more {
  display: flex;
  align-items: center;
  gap: 21px;
}
.more__text {
  display: block;
  font-size: 0.75rem;
  font-weight: 300;
  line-height: 89%;
  text-transform: uppercase;
}
.more__img {
  display: block;
}

@media (max-width: 650px) {
  .nav__list {
    padding-top: 18px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    background-color: rgba(240, 96, 170, 0.8117647059);
    transform: translateY(-120%);
  }
  .nav__list.active {
    transform: none;
  }
  .nav__link {
    color: #EFE7EE;
  }

  .burger {
    width: 30px;
    height: 20px;
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 2;
  }
  .burger span {
    position: absolute;
    top: 9px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #f060ab;
  }

  .burger.active span {
    opacity: 0;
  }

  .burger::after,
.burger::before {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #f060ab;
    transition: all 0.3s linear;
  }

  .burger::before {
    top: 0;
  }

  .burger::after {
    bottom: 0;
  }

  .burger.active::before {
    transform: rotate(405deg);
    top: 9px;
    background-color: #EFE7EE;
  }

  .burger.active::after {
    transform: rotate(315deg);
    bottom: 9px;
    background-color: #EFE7EE;
  }

  .first-screen {
    padding: 0 20px;
  }

  .box {
    margin-top: 130px;
  }

  .text {
    font-size: 0.75rem;
  }

  .logo {
    flex-basis: 20%;
    min-width: 80px;
  }
  .logo img {
    width: 100%;
  }
}
.link {
  margin-top: 200px;
}
.link__img {
  width: 30%;
  display: block;
  min-width: 250px;
  margin: 20px auto;
}
.link__title {
  text-align: center;
  margin-bottom: 10px;
}
.link__text {
  max-width: 600px;
  text-align: center;
  margin: 0 auto;
}